/* You can add global styles to this file, and also import other style files */
body{
  font-family: "Public Sans Web, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol" !important;
}
[art-generic-text-layout="top-border"] {
    border: 1px solid lightgray;
    border-top: 3px solid #162E51;
    border-radius: 5px;
}
  
[art-generic-text-layout="background-color"] {
background: #f5f9fc;
}
  
[art-generic-text-layout="border"] {
border: 1px solid #71767a;
border-radius: 0.5rem;
}
a {
    color: #1e6ad6;
}

.art-flex{
  display: flex;
}
